<template>
  <div>
    <main class="content container" v-if="cartLoading">Загрузка корзины с товарами...</main>
    <main class="content container" v-else-if="cartLoadingFailed">
      Не удалос загрузить корзину с товарами
    </main>
    <main class="content container" v-else>
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link" href="index.html">
              Каталог
            </a>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">
              Корзина
            </a>
          </li>
        </ul>

        <h1 class="content__title">
          Корзина
        </h1>
        <span class="content__info">
          {{ products.length }} товара
        </span>
      </div>

      <section class="cart">
        <form class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <ul class="cart__list">
              <CartItem v-for="item in products" :key="item.productId" :item="item"/>
            </ul>
          </div>

          <div class="cart__block">
            <p class="cart__desc">
              Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
            </p>
            <p class="cart__price">
              Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
            </p>

            <router-link tag="button" :to="{name: 'order'}"
            class="cart__button button button--primery" type="submit">
              Оформить заказ
            </router-link>

          </div>
        </form>
      </section>
    </main>
  </div>

</template>

<script>
import { mapGetters } from 'vuex';
import CartItem from '@/components/CartItem.vue';
import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  data() {
    return {
      cartData: null,
      cartLoading: false,
      cartLoadingFailed: false,
    };
  },
  filters: {
    numberFormat,
  },
  components: {
    CartItem,
  },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
  },
  methods: {
    loadCart() {
      this.cartLoading = true;
      this.cartLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadCartTimer = setTimeout(() => {
        return axios.get(`${API_BASE_URL}/api/baskets`)
          .then((response) => this.cartData = response.data)
          .catch(() => this.cartLoadingFailed = true)
          .then(() => this.cartLoading = false);
      }, 0);
    },
  },
  created() {
    this.loadCart();
  },
};
</script>
