<template>
  <router-link :to="{name: 'cart'}" class="header__cart" aria-label="Корзина с товарами">
    <svg width="30" height="21" fill="currentColor" >
      <use xlink:href="#icon-cart"></use>
    </svg>
    <img class="header__count" width="18" height="9"
    style="background-color: transparent;"
    src="../../public/img/Spinner-5.gif" alt="loading"
    v-if="cartLoading">
    <span class="header__count" aria-label="Количество товаров" v-else>
      {{ $store.state.cartProducts.length }}
    </span>
  </router-link>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  data() {
    return {
      cartLoading: false,
      cartLoadingFailed: false,
    };
  },
  methods: {
    loadCart() {
      this.cartLoading = true;
      this.cartLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadCartTimer = setTimeout(() => {
        return axios.get(`${API_BASE_URL}/api/baskets`)
          .then((response) => this.cartData = response.data)
          .catch(() => this.cartLoadingFailed = true)
          .then(() => this.cartLoading = false);
      }, 0);
    },
  },
  created() {
    this.loadCart();
  },
};
</script>
