<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        3 товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText title="ФИО" :error="formError.name"
            v-model="formData.name" placeholder="Введите ваше полное имя"/>

            <BaseFormText title="Адрес доставки" :error="formError.address"
            v-model="formData.address" placeholder="Введите ваш адрес"/>

            <BaseFormText title="Телефон" :error="formError.phone"
            v-model="formData.phone" placeholder="Введите ваш телефон"/>

            <BaseFormText title="Email" :error="formError.email"
            v-model="formData.email" placeholder="Введи ваш Email"/>

            <BaseFormTextarea title="Комментарий к заказу" :error="formError.comment"
            placeholder="Ваши пожелания" v-model="formData.comment"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0"
                  :checked="true">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500"
                  :disabled="true">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card"
                  :checked="true">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash"
                  :disabled="true">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderCart v-for="item in products" :key="item.productId" :item="item"/>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>
              Итого:
              <b>{{ cartAmount }}</b> товара на сумму
              <b>{{ totalPrice | numberFormat }}</b>
            </p>
          </div>

          <button class="cart__button button button--primery" type="submit">
              Оформить заказ
          </button>
        </div>

        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>

        <div style="display: flex;flex-direction: column-reverse;align-items: center;"
         v-if="orderStatus">
          <p>
            Оформляем заказ...
          </p>
          <img src="../../public/img/Spinner-5.gif" alt="loading">
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import OrderCart from '@/components/OrderCart.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { mapGetters } from 'vuex';
import numberFormat from '@/helpers/numberFormat';

export default {
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      orderStatus: false,
      cartData: null,
      cartLoading: false,
      cartLoadingFailed: false,
    };
  },
  components: {
    BaseFormText,
    BaseFormTextarea,
    OrderCart,
  },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
    cartAmount() {
      return this.products.length;
    },
  },
  filters: {
    numberFormat,
  },
  methods: {
    loadCart() {
      this.cartLoading = true;
      this.cartLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadCartTimer = setTimeout(() => {
        return axios.get(`${API_BASE_URL}/api/baskets?`)
          .then((response) => this.cartData = response.data)
          .catch(() => this.cartLoadingFailed = true)
          .then(() => this.cartLoading = false);
      }, 0);
    },
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      return axios.post(`${API_BASE_URL}/api/orders`, {
        ...this.formData,
      }, {
        params: {
          userAccessKey: this.$store.state.userAccessKey,
        },
      })
        .then((response) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
          this.orderStatus = true;
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
        });
    },
  },
  created() {
    this.loadCart();
  },
};
</script>
